<template>
  <div class="basic-info">
    <div class="basic-info-header">
      <div class="basic-info-header__avatar">
        <svg-icon icon-class="organization"></svg-icon>
      </div>
      <div class="basic-info-header__mid flex flex-col flex-jus-between">
        <div class="title">{{ basicInfo.shortCode }} - {{ basicInfo.organizationName }}</div>
        <div class="desc">
          Identity Status: <span class="status">{{ getIdentityStatusLabel(basicInfo.status) }}</span>
        </div>
      </div>
      <div class="basic-info-header__operator">
        <el-button type="text" @click="gotoDetailPage"
          >More <i class="el-icon-arrow-right" style="vertical-align: middle"></i
        ></el-button>
      </div>
    </div>
    <el-divider></el-divider>
    <basic-info-group :basicInfo="configMap" :groups="groups"></basic-info-group>
  </div>
</template>

<script>
import { getIdentityStatusName } from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import BasicInfoGroup from '@/views/identifier-center/pages/organization/components/basic-info-group';

export default {
  name: 'OrgChildBasicInfo',

  components: {
    BasicInfoGroup,
  },

  inject: ['detailRoot'],

  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      groups: [],
      configMap: {},
    };
  },
  watch: {
    basicInfo: {
      handler() {
        this.resetConfig();
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    resetConfig() {
      const groups = [
        {
          label: 'Basic Info',
          list: [
            {
              name: 'parentShortCode',
              label: this.$t('IDC.organization.parentShortCode'),
            },
            {
              name: 'identityModel',
              label: this.$t('IDC.organization.identityModel'),
            },
            {
              name: 'hierarchyLevel',
              label: this.$t('IDC.organization.hierarchyLevel'),
            },
            {
              name: 'topOrganization',
              label: this.$t('IDC.organization.topOrganization'),
            },
            {
              name: 'organizationName',
              label: this.$t('IDC.organization.organizationName'),
            },
            {
              name: 'shortCode',
              label: this.$t('IDC.organization.shortCode'),
            },
            {
              name: 'identityStatus',
              label: this.$t('IDC.organization.IdentityStatus'),
            },
            {
              name: 'segment',
              label: 'Segment',
            },
            {
              name: 'chargeProfile',
              label: 'Charge Profile',
            },
            {
              name: 'ruleProfile',
              label: 'Rule Profile',
            },
            {
              name: 'trustLevel',
              label: 'Trust Level ',
            },
            {
              name: 'registrationDate',
              label: this.$t('IDC.organization.registrationDate'),
            },
          ],
        },
      ];
      this.groups = groups;
      this.configMap = this.basicInfo;
    },
    getIdentityStatusLabel(val) {
      return getIdentityStatusName(val);
    },
    gotoDetailPage() {
      const id = this.basicInfo.organizationId;

      if (!id) {
        return;
      }
      const params = {
        identityId: id,
        organizationId: id,
        propShortCode: this.basicInfo.shortCode,
      };
      this.openNewWindow({ path: 'detail', query: { data: encodeURIComponent(JSON.stringify(params)) } });
    },
  },
};
</script>

