  .basic-info-header {
    display: flex;

    &__avatar {
      width: 64px;
      height: 64px;
      font-size: 40px;
      text-align: center;
      line-height: 64px;
      background-color: $--primary_color--disable;
      color: $--primary_color--hover;
      border-radius: 4px;
    }

    &__mid {
      flex: 1;

      .page-header {
        min-height: auto !important;
      }
    }
  }

  .basic-info-header__mid {
    padding-left: 16px;
    /deep/ .page-header {
      padding-top: 0;
    }
    .title {
      line-height: 32px;
      font-weight: bold;
      font-size: 24px;
      color: $--text_color--emphasis;
    }
    .desc {
      line-height: 22px;
