<template>
  <div :class="['input-rest', inputBindClass]">
    <el-input v-model="value" v-bind="$attrs" @focus="focus" @blur="blur" @change="change" @input="onInput">
      <span slot="suffix" v-show="value" @click="btnClearable">
        <i class="el-icon-close" style="margin-left: 5px; cursor: pointer"></i>
      </span>
      <div slot="append">
        <div class="append-wrapper">
          <div class="line" v-show="showLine && value"></div>
          <span @click="iconClick">
            <slot></slot>
          </span>
        </div>
      </div>
    </el-input>
  </div>
</template>

<script>
export default {
  name: 'InputWithAppend',
  props: {
    defaultValue: {
      type: String,
      default: '',
    },
    iconClick: {
      type: Function,
      default: () => {},
    },
    inputChange: {
      type: Function,
      default: () => {},
    },
    showLine: {
      type: Boolean,
      default: () => true,
    },
    needListenInput: {
      Type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      value: this.defaultValue,
      inputBindClass: 'blur',
    };
  },
  methods: {
    change(val) {
      this.inputChange(val);
    },
    onInput(val) {
      if (this.needListenInput) {
        this.inputChange(val);
      }
    },
    focus() {
      this.inputBindClass = 'focus';
    },
    blur() {
      this.inputBindClass = 'blur';
    },
    btnClearable() {
      this.change('');
    },
  },
  watch: {
    defaultValue(val) {
      this.value = val;
    },
  },
};
</script>

<style scoped lang="scss">
.input-rest {
  width: 100%;
  border: 1px solid #dcdfe6;
  border-radius: 4px;

  /deep/ .el-input__suffix {
    right: 0;
    display: flex;
    align-items: center;
  }

  /deep/ .el-input-group__append {
    padding: 0;
    cursor: pointer;
    border: none;
    background: transparent;
  }
  /deep/ .el-input__inner {
    border: none;
  }
  .append-wrapper {
    display: flex;
    align-items: center;
    padding: 0 12px;
    gap: 12px;
    .line {
      width: 1px;
      height: 16px;
      background: $--line_color--regular;
    }
  }
}

.input-rest:hover {
  border: 1px solid #c0c4cc;
}
.input-rest.focus {
  border: 1px solid #409eff;
}
/deep/ .el-input__suffix {
  transform: translateX(0);
}

.el-icon-close:before {
  content: '\E78D';
}
</style>
