import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';

export const IDENTITY_STATUS_TYPE = {
  NEW: '00',
  PENDING_ACTIVE: '02',
  ACTIVE: '03',
  SUSPENDED: '04',
  FROZEN: '05',
  CLOSED: '06',
  CAPPED: '07',
  DORMANT: '08',
};

export function getIdentityStatusName(val) {
  return STATUS_MAP[val] || '-';
}
export function isAllowedEdit(status) {
  const STATUS_ALLOWED_MAP = {
    [IDENTITY_STATUS_TYPE.ACTIVE]: 'ACTIVE',
    [IDENTITY_STATUS_TYPE.SUSPENDED]: 'SUSPENDED',
    [IDENTITY_STATUS_TYPE.FROZEN]: 'FROZEN',
  };
  return STATUS_ALLOWED_MAP[status];
}

export const IDENTITY_MODEL = {
  CAN_MAKE_AGGREGATOR: '0000',
  CAN_MAKE_OWNED: '0001',
  CAN_MAKE_ALLTYPE: '0002',
  NONEHIERARCHY: '0003',
  AGGREGATOR: '0100',
  CENTRAL_OWNED: '0101',
};

export const getSubAgentModelName = (val) => {
  switch (val) {
    case IDENTITY_MODEL.CAN_MAKE_AGGREGATOR:
      return 'Can Make Aggregator';
    case IDENTITY_MODEL.CAN_MAKE_OWNED:
      return 'Can Make Owned';
    case IDENTITY_MODEL.CAN_MAKE_ALLTYPE:
      return 'Can Make AllType';
    case IDENTITY_MODEL.NONEHIERARCHY:
      return 'NoneHierarchy';
    case IDENTITY_MODEL.AGGREGATOR:
      return 'Aggregator';
    case IDENTITY_MODEL.CENTRAL_OWNED:
      return 'Central Owned';
    default:
      return;
  }
};
