<template>
  <div>
    <portal-header :projectObj="titleConfig" />
    <div class="create-child-organization">
      <div class="create-left">
        <org-child-tree :organizationId="organizationId" :activeTabName.sync="activeTabName" />
      </div>

      <transition name="load-transform" mode="out-in">
        <org-child-basic-info
          :basic-info="activedBasicInfo"
          v-show="activeTabName === IDENTITY_STATUS_TYPE.ACTIVE"
          v-loading="activedLoading"
        />
      </transition>
      <transition name="load-transform" mode="out-in">
        <org-child-basic-info
          :basic-info="closedBasicInfo"
          v-show="activeTabName === IDENTITY_STATUS_TYPE.CLOSED"
          v-loading="closedLoading"
        />
      </transition>
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header/index.vue';
import OrgChildTree from '@/views/identifier-center/pages/organization/components/org-child-tree';
import OrgChildBasicInfo from '@/views/identifier-center/pages/organization/components/org-child-basic-info';
import { getOrganizationChildOrgDetail } from '@/views/identifier-center/api/api-organization.js';
import { dealApiRes } from '@/views/identifier-center/pages/organization/utils/common';
import {
  getIdentityStatusName,
  getSubAgentModelName,
  IDENTITY_STATUS_TYPE,
} from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';

export default {
  name: 'CreateOrganization',

  components: {
    OrgChildBasicInfo,
    OrgChildTree,
    PortalHeader,
  },

  provide() {
    return {
      childrenRoot: this,
    };
  },
  inject: ['detailRoot'],

  data() {
    return {
      IDENTITY_STATUS_TYPE,
      activeTabName: IDENTITY_STATUS_TYPE.ACTIVE,
      titleConfig: {
        title: this.$t('IDC.organization.children.title'),
        desc: this.$t('IDC.organization.children.desc'),
      },
      activedLoading: false,
      closedLoading: false,
      activedBasicInfo: {},
      closedBasicInfo: {},
    };
  },
  computed: {
    organizationId() {
      const parent = this.detailRoot;
      if (!parent.routeParams && !parent.routeParams.organizationId) {
        return;
      }
      const organizationId = parent.routeParams.organizationId;
      return organizationId;
    },
    propShortCode() {
      const parent = this.detailRoot;
      if (!parent.routeParams && !parent.routeParams.propShortCode) {
        return;
      }
      const propShortCode = parent.routeParams.propShortCode;
      return propShortCode;
    },
  },

  mounted() {},

  methods: {
    async setDetailConfig(id, activeTabName) {
      if (!id) {
        if (activeTabName === IDENTITY_STATUS_TYPE.ACTIVE) {
          this.activedBasicInfo = {};
        } else {
          this.closedBasicInfo = {};
        }
        return;
      }

      if (activeTabName === IDENTITY_STATUS_TYPE.ACTIVE) {
        this.activedLoading = true;
      } else {
        this.closedLoading = true;
      }

      const config = await this.getOrgDetailById(id);

      if (activeTabName === IDENTITY_STATUS_TYPE.ACTIVE) {
        this.activedLoading = false;
      } else {
        this.closedLoading = false;
      }

      if (!config) {
        if (activeTabName === IDENTITY_STATUS_TYPE.ACTIVE) {
          this.activedBasicInfo = {};
        } else {
          this.closedBasicInfo = {};
        }
        return;
      }

      const basicInfo = {
        parentShortCode: config.parentShortCode,
        identityModel: getSubAgentModelName(config.identityModel),
        hierarchyLevel: config.hierLevel,
        topOrganization: config.topOrganization,
        organizationName: config.organizationName,
        shortCode: config.shortCode,
        identityStatus: getIdentityStatusName(config.status),
        chargeProfile: config.chargeProfileName,
        ruleProfile: config.ruleProfileName,
        registrationDate: this.$options.filters['localTimeFormat'](config.createTime, 'DD-MM-YYYY'),
        organizationId: config.organizationId,
        status: config.status,
        segment: config.segment,
        trustLevel: config.trustLevelName,
      };

      if (activeTabName === IDENTITY_STATUS_TYPE.ACTIVE) {
        this.activedBasicInfo = basicInfo;
      } else {
        this.closedBasicInfo = basicInfo;
      }
    },

    getOrgDetailById(id) {
      return dealApiRes(getOrganizationChildOrgDetail, 'organizationInfo', { organizationId: id });
    },
  },
};
</script>

<style lang="scss" scoped>
.create-child-organization {
  background: $--text_color--white;
  display: flex;
  height: calc(100vh - 198px);
  margin-bottom: 20px;
  .create-left {
    border-right: 1px solid $--fill_color--regular;
    width: 268px;
    padding: 20px 0 0;
    box-sizing: border-box;
  }

  /* load-transform */
  .load-transform-leave-active {
    transition: all 0.1s ease-out;
  }
  .load-transform-enter-active {
    transition: all 0.5s ease-out;
  }

  .load-transform-enter {
    opacity: 0;
  }

  .load-transform-leave-to {
    opacity: 0;
  }
}
</style>
