<template>
  <div class="c-tree">
    <div class="tree-operation">
      <input-with-append
        size="small"
        :defaultValue="filterText"
        :showLine="false"
        :inputChange="changeFilterText"
        :needListenInput="true"
        placeholder="Search for children..."
      >
        <i class="el-icon-search" @click="searchCurrentTreeNodes"></i>
      </input-with-append>
      <svg-icon icon-class="augment" class="icon-btn--create" @click="addChildOrgHandle"></svg-icon>
    </div>
    <el-tabs
      :stretch="true"
      v-model="tabName"
      :class="{
        'el-tabs-custom': true,
        'el-tabs-custom--effective': tabName === IDENTITY_STATUS_TYPE.ACTIVE,
        'el-tabs-custom--closed': tabName === IDENTITY_STATUS_TYPE.CLOSED,
      }"
      @tab-click="tabClickHandle"
    >
      <el-tab-pane label="Effective Children" :name="IDENTITY_STATUS_TYPE.ACTIVE">
        <div class="tree-container">
          <el-tree
            ref="activedTree"
            empty-text="No records found."
            :filter-node-method="filterTreeNode"
            :highlight-current="true"
            :expand-on-click-node="false"
            @node-click="(data) => handleNodeClick(data, IDENTITY_STATUS_TYPE.ACTIVE)"
            node-key="organizationId"
            :props="defaultProps"
            :indent="24"
            :load="loadNode"
            :data="effectiveData"
            lazy
            v-if="organizationId"
          />
        </div>
      </el-tab-pane>
      <el-tab-pane label="Closed Children" :name="IDENTITY_STATUS_TYPE.CLOSED">
        <div class="tree-container">
          <el-tree
            ref="closedTree"
            empty-text="No records found."
            :default-expand-all="true"
            :filter-node-method="filterTreeNode"
            :highlight-current="true"
            node-key="organizationId"
            @node-click="(data) => handleNodeClick(data, IDENTITY_STATUS_TYPE.CLOSED)"
            :data="closedTreeData"
            :props="defaultProps"
          ></el-tree>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import InputWithAppend from './input-with-append.vue';
import { queryChildren } from '@/views/identifier-center/api/api-organization';
import { IDENTITY_STATUS_TYPE } from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import { dealApiRes } from '@/views/identifier-center/pages/organization/utils/common';
import { getOrganizationDetail } from '@/views/identifier-center/api/api-organization';

export default {
  name: 'CreateInfo',
  components: {
    InputWithAppend,
  },
  inject: ['childrenRoot'],
  props: {
    organizationId: {
      type: String,
      required: true,
    },
    activeTabName: {
      type: String,
      required: true,
    },
    configLoading: {
      type: Boolean,
    },
  },
  data() {
    return {
      IDENTITY_STATUS_TYPE,
      tabName: this.activeTabName,
      closedTreeData: [],
      defaultProps: {
        children: 'children',
        isLeaf: true,
        label: function (data, node) {
          return data.shortCode + '-' + data.organizationName;
        },
      },
      filterText: '',
      closedfilterText: '',
      effectiveData: [],
      orgInfo: {},
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.setClosedTreeData();
      this.loadOrganizationInfo();
    },
    async loadOrganizationInfo() {
      this.orgInfo = await getOrganizationDetail({ identityId: this.childrenRoot.organizationId });
    },
    tabClickHandle() {
      this.$emit('update:activeTabName', this.tabName);
      this.$nextTick(() => {
        this.changeFilterText(this.filterText);
      });
    },
    async setClosedTreeData() {
      const organizationId = this.organizationId;
      if (!organizationId) {
        return;
      }
      const params = {
        organizationId,
        isAllChildren: true,
        status: IDENTITY_STATUS_TYPE.CLOSED,
      };
      const data = await this.queryTree(params);
      this.closedTreeData = data;
      if (data && data.length) {
        this.$nextTick(() => {
          const treeRef = this.$refs.closedTree;
          treeRef &&
            treeRef.$nextTick(() => {
              this.setCurrentNode(treeRef, data[0], IDENTITY_STATUS_TYPE.CLOSED);
            });
        });
      }
    },
    loadNode(node, resolve) {
      if (!this.organizationId) {
        return resolve([]);
      }
      let id = '';
      let level = 0;

      if (node.level === 0) {
        id = this.organizationId;
      }
      if (node.level >= 1) {
        id = node.data.organizationId;
        level = node.level;
      }

      if (!id) {
        return resolve([]);
      }
      this.getChildTreeAndDetail(id, level, resolve);
    },
    async handleNodeClick(data, status) {
      const id = data.organizationId;
      if (!id) {
        return;
      }
      this.childrenRoot.setDetailConfig(id, status); // parent method
    },

    filterTreeNode(value, data) {
      if (!value) return true;
      const text = value.toLocaleLowerCase();

      let label = '';
      const shortCode = data['shortCode'];
      const organizationName = data['organizationName'];
      if (shortCode) {
        label += shortCode.toLocaleLowerCase();
      }
      if (organizationName) {
        label += '-' + organizationName.toLocaleLowerCase();
      }
      return label.indexOf(text) !== -1;
    },

    setCurrentNode(treeRef, data, status) {
      this.$nextTick(() => {
        treeRef.$nextTick(() => {
          treeRef.setCurrentNode(data);
          this.handleNodeClick(data, status);
        });
      });
    },
    async getChildTreeAndDetail(organizationId, level, resolve) {
      const childrenRoot = this.childrenRoot;
      if (!organizationId) {
        resolve([]);
        childrenRoot.activedLoading = false;
        return;
      }

      if (level === 0) {
        childrenRoot.activedLoading = true;
      }

      const params = {
        organizationId,
        isAllChildren: false,
        status: IDENTITY_STATUS_TYPE.ACTIVE,
      };

      const data = await this.queryTree(params);

      resolve(data || []);

      if (data && data.length && level === 0) {
        this.$nextTick(() => {
          const treeRef = this.$refs.activedTree;
          treeRef &&
            treeRef.$nextTick(() => {
              this.setCurrentNode(treeRef, data[0], IDENTITY_STATUS_TYPE.ACTIVE);
            });
        });
      }
      if (level === 0) {
        childrenRoot.activedLoading = false;
      }
    },
    async queryTree(params) {
      const data = await dealApiRes(queryChildren, 'list', params);
      return data || [];
    },
    addChildOrgHandle() {
      if (this.orgInfo.organizationInfo.subAgentExpansionModel == '0003') {
        this.$message.error('Invalid subAgentExpansionModel not allow sub-organizations.');
      } else {
        // create 页面路由定义在当前页面的父级
        this.openNewWindow({
          path: 'create',
          query: {
            parentShortCode: this.childrenRoot.propShortCode,
            parentIdentityId: this.childrenRoot.organizationId,
          },
        });
      }
    },
    changeFilterText(data) {
      this.filterText = data;
      if (IDENTITY_STATUS_TYPE.ACTIVE === this.tabName) {
        this.$refs.activedTree.filter(data);
      }
      if (IDENTITY_STATUS_TYPE.CLOSED === this.tabName) {
        this.$refs.closedTree.filter(data);
      }
    },
    async searchCurrentTreeNodes() {
      let status = this.tabName === IDENTITY_STATUS_TYPE.ACTIVE ? '03' : '06';

      const params = {
        organizationId: this.childrenRoot.organizationId,
        allChildren: true,
        status,
        shortCode: this.filterText,
      };
      const res = await this.queryTree(params);
      this.effectiveData = res;
      this.closedTreeData = res;
      let firstNode = res[0];
      this.getChildTreeAndDetail(firstNode.organizationId);
    },
  },
};
</script>
<style scoped lang="scss">
