    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: 10px;
    padding-right: 15px;
    margin-bottom: 12px;
    box-sizing: border-box;

    .icon-btn--create {
      font-size: 22px;
      color: $--primary_color;
      cursor: pointer;
    }
  }

  .tree-container {
    height: calc(100vh - 250px);
    overflow: auto;
    padding-left: 3px;
    padding-right: 5px;

    .el-tree {
      width: max-content;
      min-width: 100%;
    }

    /deep/ .el-tree-node__expand-icon {
      position: relative;
      width: 16px;
      height: 16px;
      margin-right: 8px;
      margin-left: 16px;
      background: unset;
      &::before {
        position: absolute;
        top: 0;
        left: 0;
        content: '\e674';
        font-size: 16px;
        color: $--text_color--emphasis;
      }
    }
    /deep/ .el-tree-node__expand-icon.expanded {
      transform: rotate(90deg);
    }
    /deep/ .el-tree-node__expand-icon.is-leaf {
      &::before {
        content: '';
      }
    }
    /deep/ .el-tree-node__content {
      height: 32px;
    }
    /deep/ .el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content {
      background-color: $--fill_color--regular;
    }
  }

  .el-tabs-custom {
    /deep/ {
      .el-tabs__header {
        margin-bottom: 0;
        .el-tabs__item {
          padding: 0 !important;
          font-size: 12px;
          color: $--text_color--secondary;
          border: none;
        }

        .el-tabs__item.is-active {
          border-right-color: $--text_color--white;
          border-left-color: $--text_color--white;
          border-bottom-color: $--text_color--white;
          color: $--primary_color !important;
        }
      }

      .el-tabs__nav-wrap::after {
        height: 1px;
        background-color: $--fill_color--regular;
      }
    }
    /deep/ .el-tabs__header .el-tabs__item.is-active {
      border-right-color: $--text_color--white;
      border-left-color: $--text_color--white;
      border-bottom-color: $--text_color--white;
      color: $--primary_color !important;
    }
    .el-tree {
      margin-top: 16px;
    }
  }

  .el-tabs-custom--effective {
    /deep/ .el-tabs__active-bar {
      width: 15% !important;
      transform: translateX(120%) !important;
    }
  }
  .el-tabs-custom--closed {
    /deep/ .el-tabs__active-bar {
      width: 15% !important;
      transform: translateX(450%) !important;
    }
  }
}
</style>
